<template>
  <div class="relation-container" id="myChart">
      relation-container
  </div>
</template>
<script>

import {
  TitleComponent,
  TooltipComponent,
  LegendComponent
} from 'echarts/components';
import { GraphChart } from 'echarts/charts';
import { CanvasRenderer } from 'echarts/renderers';
import graph from '@/assets/les-miserables.json'
export default {
  name: 'RealtionShip',
  components: {},
  data() {
    return {

    }
  },
  computed: {},
  watch: {},
  props: {},
  methods: {
   onLoad(){
      this.$echarts.use([
        TitleComponent,
        TooltipComponent,
        LegendComponent,
        GraphChart,
        CanvasRenderer
      ]);


      var myChart = this.$echarts.init(document.getElementById('myChart'));
      var option;
      myChart.showLoading();      
      myChart.hideLoading();
      graph.nodes.forEach(function (node) {
        node.label = {
          show: node.symbolSize > 30
        };
      });
      option = {
        title: {
          text: 'Les Miserables',
          subtext: 'Default layout',
          top: 'bottom',
          left: 'right'
        },
        tooltip: {},
        legend: [
          {
            // selectedMode: 'single',
            data: graph.categories.map(function (a) {
              return a.name;
            })
          }
        ],
        animationDuration: 1500,
        animationEasingUpdate: 'quinticInOut',
        series: [
          {
            name: 'Les Miserables',
            type: 'graph',
            layout: 'none',
            data: graph.nodes,
            links: graph.links,
            categories: graph.categories,
            roam: true,
            label: {
              position: 'right',
              formatter: '{b}'
            },
            lineStyle: {
              color: 'source',
              curveness: 0.3
            },
            emphasis: {
              focus: 'adjacency',
              lineStyle: {
                width: 10
              }
            }
          }
        ]
      };
      myChart.setOption(option);
      // option 
      // myChart.setOption(option);

    }
  },
  mounted() {
    this.onLoad()
  },
}
</script>

<style scoped lang="less">
.relation-container{
  width: 100%;
  height: 500px;
}
</style>
